importScripts('./ffmpeg.js');

function print(text) {
    console.log(text);
}

onmessage = function (event) {
    var now = Date.now;
    var message = event.data;

    if (message.type === "command") {
        var Module = {
            print: print,
            printErr: print,
            files: message.files || [],
            arguments: message.arguments
        };

        var time = now();
        var result = ffmpeg_run(Module, function (result) {
            var totalTime = now() - time;

            console.log('result totalTime:', totalTime);

            postMessage({
                'type': 'done',
                'data': result,
                'time': totalTime
            });
        });
    }
};